.header {
}

.top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #e1e1e1;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  // .top__left

  &__left {
    display: flex;
    align-items: center;
  }
  &__left-list {
    display: flex;
  }

  &__left-item {
    margin-right: 50px;
    font-weight: 500;
    img {
      margin-right: 5px;
    }
  }

  // .top__right

  &__right {
    display: flex;
    align-items: center;
  }
  &__right-list {
    display: flex;
    margin-right: 10px;
  }
  &__right-item {
    margin-right: 20px;
    font-weight: 600;
  }
  &__right-button {
    display: inline-flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 5px;
    height: 47px;
    width: 100px;
    border-left: 1px solid #a7a7a7;
    font-weight: 500;
    font-size: 14px;
    svg {
      margin-right: 5px;
    }
  }
}

.middle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;

  // .middle__item

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__logo {
    margin-right: 45px;
    cursor: pointer;
  }
  &__logolink {
    // display: inline-flex;
    margin-right: 10px;
  }

  &__logotext {
    font-size: 12px;
    font-weight: 500;
    line-height: 1.4;
    max-width: 125px;
  }
  // .middle__link
  &__input {
    position: relative;
    width: 781px;
    margin-right: 40px;
    input {
      width: 100%;
      border: 1px solid var(--red-color);
      border-radius: 4px;
      padding: 12px 20px;
    }
    svg {
      position: absolute;
      top: 15px;
      right: 15px;
      fill: var(--red-color);
    }
    &::placeholder {
      font-size: 12px;
      color: #ababab;
    }
  }
  &__phone {
    position: relative;
    align-items: flex-start;
    img {
      position: absolute;
      top: 5px;
      left: -30px;
    }
  }

  &__link {
    color: var(--red-color);
    display: inline-block;
    max-width: 200px;
    font-size: 24px;
    font-weight: 700;
    line-height: 1;
    margin-right: 34px;
    span {
      font-size: 12px;
      font-weight: 600;
      border-bottom: 1px solid var(--red-color);
    }
  }
}

.social {
  display: flex;
  align-items: center;
  // .social__item

  &__item {
    margin-right: 7px;
  }
}
.bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #f3f3f3;
  // .bottom__btn

  &__btn {
    width: 164px;
    height: 71px;
    background: var(--red-color);
    border-radius: 10px;
    font-size: 22px;
    color: #fff;
    font-weight: 700;
    text-transform: uppercase;
  }

  // .bottom__list

  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  // .bottom__item

  &__item {
    margin-right: 30px;
  }

  // .bottom__link

  &__link {
    font-weight: 600;
    font-size: 14px;
    text-transform: uppercase;
  }

  // .bottom__sale

  &__sale {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 71px;
    font-weight: 700;
    font-size: 18px;
    text-transform: uppercase;
    color: #d2334d;
    padding: 20px;
    border-left: 1px solid #f3f3f3;
    svg {
      margin-right: 5px;
    }
  }
}
